import React from 'react';
import { Route, Switch } from 'react-router-dom';
import { YztApp } from '../utils/native_h5';

import Home from '../views/Home';
import Article from '../views/Article';
import Flash from '../views/Flash';
import Detail from '../views/Detail';
import Encyclopedia from '../views/Encyclopedia';
import Navigation from '../views/Navigation';

import { NavigationBar } from '../components';

class Layout extends React.Component {
    componentWillMount() {
        // YztApp.showOrHideNavMoreBtn(false);
        // YztApp.isNeedBack();
    }
    componentDidMount() {
        this.updatePageTitle(this.props.location.pathname);
    }
    componentWillUpdate(nextProps) {
        const oldPathName = this.props.location.pathname;
        const newPathName = nextProps.location.pathname;
        if (oldPathName !== newPathName) {
            this.updatePageTitle(newPathName)
        }

    }
    updatePageTitle(pathname) { // 更新页面 title
        switch (pathname) {
            case '/':
                YztApp.setTitle('网站标题');
                break;
            case '/article':
                YztApp.setTitle('网站标题');
                break;
            case '/flash':
                YztApp.setTitle('网站标题');
                break;
        }
    }
    render() {
        return (
            <div className="page-container">
                <NavigationBar />
                <div className="view-container">
                    <Switch>
                        <Route exact path="/" component={Home} title="首页" />
                        <Route path="/article" component={Article} title="文章页" />
                        <Route path="/flash" component={Flash} title="24h快讯" />
                        <Route path="/detail" component={Detail} title="币种详情页" />
                        <Route path="/encyclopedia" component={Encyclopedia} title="币百科" />
                        <Route path="/navigation" component={Navigation} title="网址导航" />
                    </Switch>
                </div>
            </div>
        )
    }
}

export default Layout;


